.header {
    position: sticky;
    top: 0;
    display: flex;
    border-bottom: 1px solid lightgrey;
    background: #fff;
    justify-content: space-evenly;
    padding: 10px 0;
    width: 100%;
    z-index: 999;
}

.header_left {
    display: flex;
}

.header_left > img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
}
.header_search {
    display: flex;
    padding: 10px;
    align-items: center;
    border-radius: 5px;
    height: 40px;
    color: gray;
    background: #eef3f8;
}

.header_search > input {
    background: 0;
    outline: none;
    border: none;
    padding-left: 5px;
}

.header_right {
    display: flex;
}
